.supportlandingpage{
    width: 100%;
}

.supportlandingpage img{
    height: 100%;
    width: 100%;
}

.support-title{
    text-align: center;
    font-size: 1.8em;
}

.support-title h1{
  font-size: 1.8em;
}

.support-volunteer{
    width: 92%;
    display: flex;
    justify-content: center;
    align-content: center;
    margin-top: 10vh;
    height: 80vh;
    margin-right: auto;
    margin-left: auto;
}

.support-volunteer-allcontent{
    display: flex;
    width: 100%;
    margin: 0 0;
}

.support-volunteer-image{
    width: 40%;
}

.support-volunteer-image img{
    width: 100%;
    height: 100%;
}

.support-volunteer-content, .support-volunteer-content1{
    width: 60%;
    text-align: center;
    padding: 0 7vw;
    background-color: #f2f2f2;
    position: relative;
}

.support-volunteer-content p, .support-volunteer-content1 p{
    margin-top: 5vh;
    font-size: 1em;
    font-weight: lighter;
}

.volunteer-title{
    font-size: 1.5em !important;
    font-weight: bolder !important;
}

.support-volunteer-content button{
    margin-top: 3vh;
    padding: 2vh 2vw;
    background-color: white;
    font-size: 1.3em;
    border-radius: 5vh;
    border: 1px solid black;
    cursor: pointer;
    transition: 0.5s ease;
    outline: none;
}

.support-volunteer-content1 button{
    margin-top: 3vh;
    padding: 2vh 2vw;
    background-color: white;
    font-size: 1.3em;
    border-radius: 5vh;
    border: 1px solid black;
    cursor: pointer;
    transition: 0.5s ease;
    outline: none;
    margin-top: 15vh;
}

.support-volunteer-content button:hover{
    background-color: rgba(224, 90, 13, 0.05);
}
.support-volunteer-content1 button:hover{
    background-color: rgba(224, 90, 13, 0.05);
}















.modalvolunteer, .modalinternship {
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
    -webkit-animation-name: fadeIn; /* Fade in the background */
    -webkit-animation-duration: 0.4s;
    animation-name: fadeIn;
    animation-duration: 0.4s;
  }
  
  /* Modal Content */
  .modal-content {
    position: fixed;
    top: 5%;
    left: 25%;
    background-color: #fefefe;
    width: 50%;
    -webkit-animation-name: slideIn;
    -webkit-animation-duration: 0.4s;
    animation-name: slideIn;
    animation-duration: 0.4s;
    height: 87vh;
  }
  
  /* The Close Button */
  .close {
    color: white;
    float: right;
    font-size: 28px;
    font-weight: bold;
    right: 0;
    width: 10%;
  }
  
  .close:hover,
  .close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
  }
  
  .modal-header {
    padding: 2px 16px;
    display: flex;
    height: 8vh;
    background-color: #E05B0D;
    text-align: center;
    color: white;
  }

  .modal-heading{
    width: 90%;
    padding-top: 1vh;
  }
  
  .modal-body {padding: 2px 16px;}
  
  .modal-footer {
    padding: 2px 16px;
    background-color: #5cb85c;
    color: white;
  }
  
.volunteer-form{
  margin-top: 3vh;
  padding-left: 7vw;
  position: absolute;
  width: 100%;
  left: 5%;
}

.volunteer-form input{
  border: none;
  border-bottom: 1px solid black;
  color: rgba(0, 0, 0, 0.712);
  outline: none;
  margin-top: 2vh;
  padding: 1vh 0 1vh 1vw;
  width: 70%;
}

.volunteer-form select{
  margin-top: 2vh;
  padding: 1vh 0 1vh 1vw;
  width: 70%;
}

.volunteer-form-submit button{
  margin-top: 3vh;
  padding: 1.5vh 2vw;
  background-color: white;
  font-size: 1.1em;
  border-radius: 5vh;
  border: 1px solid black;
  cursor: pointer;
  transition: 0.5s ease;
  outline: none;
  margin-top: 5vh;
  margin-bottom: 3vh;
  position: absolute;
  left: 35%;
}

.volunteer-form-submit button:hover{
  background-color: rgba(0, 0, 0, 0.856);
  color: white;
}

.volunteer-form-submit-msg{
  position: absolute;
  left: 25%;
}

.volunteer-form-submit-false{
  color: red;
  font-size: 1.1em;
}

.volunteer-form-submit-true{
  color: green;
  font-size: 1.1em;
}
  
