.contact-landingpage{
    height: 100vh;
    position: relative;
    width: 100%;
}

.contact-landingpage img{
    width: 100%;
}

.contact-title{
    position: absolute;
    bottom: 10vh;
    text-align: center;
    width: 100%;
    font-size: 3em;
    color: white;
}

.contact-title h1{
    font-size: 2em;
}

.contact-div{
    margin-top: 20vh;
    margin-bottom: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
}

.contact-contactform{
    display: flex;
    justify-content: center;
    align-content: center;
    width: 40%;
}

.contact-contactform-form{
    width: 100%;
}

.inputName, .inputEmail, .inputSubject, .inputMsg{
    border: none;
    border-bottom: 1px black solid;
    color: rgba(0, 0, 0, 0.849);
    padding: 1.5vh 2vh;
}

.inputName::placeholder, .inputEmail::placeholder, .inputSubject::placeholder, .inputMsg::placeholder{
    color: rgba(0, 0, 0, 0.849);
}

.inputName, .inputEmail{
    width: 48.8%;
    margin-top: 2vh;
}

.inputSubject{
    width: 100%;
    margin-top: 2vh;
}

.inputMsg{
    width: 100%;
    margin-top: 2vh;
    resize: none;
}

.contact-submit-div{
    width: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
    margin-top: 4vh;
}

.contact-submit-div button{
    padding: 2vh 8vh;
    border: 2px solid #E05B0D;
    background-color: white;
    font-size: 1.1em;
    border-radius: 5vh;
    cursor: pointer;
    transition: 0.5s ease;
    outline: none;
}

.contact-submit-div button:hover{
    background-color: black;
    border-color: black;
    color: white;
}

.contact-msg{
    text-align: center;
    width: 100%;
}

.contact-msg-true{
    color: green;
    font-size: 1em;
}

.contact-msg-false{
    font-size: 1em;
    color: red;
}