.about-first{
    height: 110vh;
    background-color: #e5e5e4;
}

.about-sanjaySir-text{
    height: 100%;
    position: relative;
    z-index: 9;
}

.about-sanjaySir-text-name{
    font-size: 6em;
    width: 50%;
    text-align: right;
    position: absolute;
    top: 5%;
    right: 48%;
}

.about-sanjaySir-text-tag{
    position: absolute;
    top: 60vh;
    left: 40.35%;
}

.about-sanjaySir-text-quote{
    position: absolute;
    width: 55%;
    font-weight: lighter;
    text-align: center;
    font-size: 1.2em;
    top: 65%;
    left: 8%;
}

.about-sanjaySir-image img{
    position: absolute;
    top: 5%;
    height: 100vh;
    width: 42vw;
    right: 13%;
}









.about-mission, .about-biharCampaign{
    margin-top: 10vh;
    height: 75vh;
    background-color: #e5e5e4;
    position: relative;
    width: 100%;
    display: flex;
}

.about-mission-image, .about-biharCampaign-image{
    position: relative;
    width: 58%;
    height: 100%;
    text-align: center;
    color: white;
}

.about-mission-image p, .about-biharCampaign-image p{
    position: absolute;
    bottom: 20%;
    z-index: 99;
    color: white;
    width: 80%;
    left:0;
    right:0;
    margin-left:auto;
    margin-right:auto;
    font-size: 5em;
}

.about-mission-image img, .about-biharCampaign-image img{
    width: 100%;
    height: 100%;
}

.about-mission-text, .about-biharCampaign-text{
    width: 42%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-mission-text p, .about-biharCampaign-text p{
    font-weight: lighter;
    padding: 5vh 8vh;
    font-size: 1.2em;
}


.about-vision{
    margin-top: 10vh;
    height: 75vh;
    background-color: #e5e5e4;
    position: relative;
    width: 100%;
    display: flex;
}

.about-vision-image{
    position: relative;
    width: 58%;
    height: 100%;
    text-align: center;
    color: white;
}

.about-vision-image p{
    position: absolute;
    bottom: 20%;
    z-index: 99;
    color: white;
    width: 100%;
    left: 0;
    font-size: 5em;
}

.about-vision-image img{
    width: 100%;
    height: 100%;
}

.about-vision-text{
    width: 42%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-vision-text p{
    font-weight: lighter;
    padding: 5vh 8vh;
    font-size: 1.2em;
}