.ourPrograms{
    text-align: center;
}

.ourPrograms{
    margin-top: 5vh;
}

.ourProgramsGridCollection{
    margin-top: 10vh;
    width: 90vw;
    height: 70vh;
    display: flex;
    justify-content: center;
    position: relative;
    margin-left: auto;
    margin-right: auto;
}

.programSingleGrid{
    cursor: pointer;
    position: relative;
    width: 30%;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.programSingleGrid-img{
    height: 65vh;
    width: auto;
    position: absolute;
    z-index: -1;
    margin-left: 50px;
    display: block;
    opacity: 1;
    transition: .5s ease;
    backface-visibility: hidden;
}

.programSingleGrid h2{
    color: white;
    margin-top: 80%;
    margin-right: 25%;
    margin-left: 25%;
}

.programName-hovereffect{
    transition: .5s ease;
    opacity: 0  ;
    text-align: center;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    z-index: 99;
    justify-content: center;
    align-items: center;
}

.program-content-learnmore{
    position: absolute;
    transition: 0.6s ease-in;
    opacity: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
    color: white;
}

.programSingleGrid:hover .program-content-learnmore{
    opacity: 1;
    cursor: pointer;
}

.programSingleGrid:hover .programName-heading{
    opacity: 0;
}

.programSingleGrid:hover .programSingleGrid-img{
    filter: brightness(70%);
    cursor: pointer;
}

@media (max-width: 768px){
    .ourPrograms{
        text-align: center;
    }
    
    .ourPrograms{
        margin-top: 5vh;
        margin-bottom: 0vh;
    }
    
    .ourProgramsGridCollection{
        margin-top: 0vh;
        width: 100%;
        height: 100%;
        display: inline-block;
        margin: 0 auto;
        position: relative;
        justify-content: none;
    }
    
    .programSingleGrid{
        cursor: pointer;
        position: relative;
        width: 70%;
        align-items: none;
        justify-content: none;
        text-align: center;
        margin: -25vh auto;
        height: 35vh;
    }
    
    .programSingleGrid-img{
        height: 100%;
        width: 100%;
        z-index: -1;
        margin-left: 0;
        display: block;
        opacity: 1;
        transition: .5s ease;
        backface-visibility: hidden;
    }
    
    .programSingleGrid h2{
        color: white;
        margin-top: 80%;
        margin-right: 0%;
        margin-left: 0%;
        padding-top: 10vh;
    }

    .programName-hovereffect{
        transition: .5s ease;
        opacity: 0  ;
        text-align: center;
        transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        z-index: 99;
        justify-content: center;
        align-items: center;
    }
    
    .program-content-learnmore{
        position: absolute;
        transition: 0.6s ease-in;
        opacity: 0;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        text-align: center;
        color: white;
    }
    
    .programSingleGrid:hover .program-content-learnmore{
        opacity: 1;
        cursor: pointer;
    }
    
    .programSingleGrid:hover .programName-heading{
        opacity: 0;
    }
    
    .programSingleGrid:hover .programSingleGrid-img{
        filter: brightness(70%);
        cursor: pointer;
    }
    
}