
.navbar{
    z-index: 999;
    position: fixed;
    height: 100vh;
    width: 20%;
    background-color: black;
    right: 0;
    line-height: 8vh;
}

.active-navlink{
    color: #e05b0d !important;
}

.navbar-links{
    position: absolute;
    top: 20%;
}

.navbar-links a{
    text-decoration: none;  
    color: white;
    font-size: 1.2em;
    display: block;
    padding-left: 3vw;
}

.navbar-links a:hover{
    color: #e05b0d;
}

.navbar-close{
    color: white;
    font-size: 2em;
    float: right;
    position: absolute;
    top: 5%;
    right: 10%;
    cursor: pointer;
}