.getinvolved{
    margin-top: 18vh;
    height: 130vh;
    width: 100%;
}

.getinvolved-heading{
    text-align: center;
}

.getinvolved-content-collection{
    margin-top: 10vh;
    justify-content: center;
    align-content: center;
    display: flex;
}

.getinvolved-content{
    position: relative;
    width: 33%;
    height: 90vh;
}

.getinvolved-content-img{
    align-self: center;
    justify-content: center;
    display: flex;
}

.getinvolved-content img{
    height: 50vh;
    width: 80%;
}

.getinvolved-content-data{
    text-align: center;
    width: 100%;
    padding: 0 4vw;
}

.getinvolved-content-data h3{
    margin-top: 3vh;
    font-weight: bold;
    font-size: 1.5em;
}

.getinvolved-content-data p{
    margin-top: 4vh;
    font-size: 1em;
    font-weight: normal;
}

.getinvolved-content-learnmore{
    margin-left: auto;
    margin-right: auto;
    position: absolute;
    width: 80%;
    height: 5vh;
    bottom: 0;
}

.getinvolved-content-learnmore a{
    color: black;
    text-decoration: none;
    width: 40%;
    height: 5vh;
    border: 2px solid #E05B0D;
    border-radius: 20px;
    padding: 1vh 2vh;
    transition: .5s ease;
}

.getinvolved-content-learnmore a:hover{
    background-color: rgba(0,0,0,0.8);
    border-color: rgba(0,0,0,0.8);
    color: white;
}

.newsletterSubscribe{
    margin-top: 10vh;
    height: 80vh;
    width: 100%;
    position: relative;
}

.newslettersubscribe-heading{ 
    text-align: center;
}

.newslettersubscribe-heading h1{
    width: 50%;
    font-size: 1.8em;
}

.newslettersubscribe-heading h2{
    margin-top: 5vh;
    padding: 0 25%;
    font-size: 1.3em;
    font-weight: lighter;
}

.newslettersubscribe-input{
    margin-top: 10vh;
    display: flex;
    justify-content: center;
    width: 100%;
    flex-wrap: wrap;
}

.newslettersubscribe-input input{
    width: 20%;
    height: 7vh;
    padding: 1.5vh 2vh;
    border: 2px solid rgba(128, 128, 128, 0.685);
    color: rgba(102, 98, 98, 0.712);
}

.subscribe-input-2{
    margin-left: 10vw;
}

.newslettersubscribe-submit{
    justify-content: center;
    display: flex;
}

.newslettersubscribe-submit button{
    cursor: pointer;
    background-color: white;
    text-align: center;
    margin-top: 10vh;
    color: black;
    text-decoration: none;
    border: 2px solid #E05B0D;
    border-radius: 40px;
    padding: 1.2vh 9vh;
    transition: .5s ease;
    font-size: 1em;
}

.newslettersubscribe-submit button:hover{
    background-color: rgba(0,0,0,0.8);
    border-color: rgba(0,0,0,0.8);
    color: white;
}

.subscribe-msgtrue{
    margin-top: 2vh;
    color: green;
    text-align: center;
    animation: ease-in 2;
}

.subscribe-msgfalse{
    margin-top: 2vh;
    color: red;
    text-align: center;
}

.footer-contactinfo{
    background-color: rgba(0, 0, 0, 0.85);
    color: white;
    font-weight: bold;
    text-align: center;
    position: absolute;
    width: 100%;
    height: 10vh;
    align-items: center;
    display: flex;
    justify-content: center;
    bottom: 0;
}



