.statsdataanimation{
    width: 25vh;
    height: 40vh;
    margin-left: 20vh;
    margin-right: 20vh;
}

.statsdata{
    background-color: #e5e5e4;
    height: 90vh;
    display: flex;
}

.heading-statsdata{
    height: 90vh;
    background-color: #e05b0d;
    width: 45vh;
    min-width: 45vh;
    margin-left: 25vh;
    color: #fff; 
    text-align: center;
    justify-content: center;
    align-items: center;
    display: flex;
    padding: 0 3vh;
}

.heading1-statsdata{    
    font-size: 2.5em;
    font-weight: bold;
}

.donut1{
    width: 100%;
    height: 100%;
    display: inline-block;
    border-radius: 50%;
    z-index: 9;
    position: absolute;
}
.donut2{
    width: 100%;
    height: 100%;
    background: radial-gradient(transparent 63%, rgba(128, 128, 128, 0.438) 010%);
    margin: 10px;
    border-radius: 50%;
    position: absolute;
    margin: 0;
}

.donut-value{
    width: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;
    display: flex;
    font-size: 2em;
    position: absolute;
    color: rgba(255, 255, 255, 0.664);
    z-index: 99;
}

.circledata-statsDataAnimation{
    height: 20vh;
    width: 20vh;
    background-color: black;
    border-radius: 10vh;
    padding: 0;
    overflow: hidden;
    position: relative;
    text-align: center;
    justify-content: center;
    align-items: center;
    display: flex;
    margin: auto;
}

.donut-below-content{
    font-size: 0.9em;
    align-self: center;
    text-align: center;
}

.stats-statsdata{
    overflow: hidden;
    padding-top: 10vh;
    display: flex;
    justify-content: center;
    justify-items: center;
    flex-wrap: wrap;
    max-width: 100%;
}

.stats-statsdatadonut{
    height: 40vh;
    width: 40vh;
}



@media (max-width:768px){
    
    .statsdata{
        background-color: #e5e5e4;
        height: 100%;
        display: block;
    }
    
    .heading-statsdata{
        height: 30vh;
        background-color: #e05b0d;
        width: 100%;
        margin: 0;   
        color: #fff; 
        text-align: center;
        justify-content: center;
        align-items: center;
        padding: 0 5vh;
    }

    .heading2-statsdata{
        font-size: 1.3em;
    }
    
    .heading1-statsdata{    
        font-size: 2.5em;
        font-weight: bold;
    }
    
    .donut1{
        width: 100%;
        height: 100%;
        display: inline-block;
        border-radius: 50%;
        z-index: 9;
        position: absolute;
    }
    .donut2{
        width: 100%;
        height: 100%;
        background: radial-gradient(transparent 63%, rgba(128, 128, 128, 0.438) 010%);
        margin: 10px;
        border-radius: 50%;
        position: absolute;
        margin: 0;
    }
    
    .donut-value{
        width: 100%;
        justify-content: center;
        align-items: center;
        text-align: center;
        display: flex;
        font-size: 2em;
        position: absolute;
        color: rgba(255, 255, 255, 0.664);
        z-index: 99;
    }
    
    .circledata-statsDataAnimation{
        height: 20vh;
        width: 20vh;
        background-color: black;
        border-radius: 10vh;
        padding: 0;
        overflow: hidden;
        position: relative;
        text-align: center;
        justify-content: center;
        align-items: center;
        display: flex;
        margin: auto;
    }
    
    .donut-below-content{
        margin-top: 1vh;
        font-size: 1em;
        align-self: center;
        text-align: center;
    }
    
    .stats-statsdata{
        overflow: hidden;
        padding-top: 10vh;
        display: flex;
        justify-content: center;
        justify-items: center;
        flex-wrap: wrap;
        max-width: 100%;
    }
    
    .stats-statsdatadonut{
        height: 40vh;
        width: 40vh;
    }
    
    .statsdataanimation{
        width: 25vh;
        height: 40vh;
        margin-left: 5vh;
        margin-right: 5vh;
    }
}