.fixed-donate{
    position: fixed;
    right: 2vw;
    top: 50%;
    z-index: 99;
    border: 1px solid black;
    border-radius: 10vh ;
    padding: 1.2vh 3.5vh;
    background-color: white;
    cursor: pointer;
    color: black;
    height: 8vh;
}

.fixed-donate:hover{
    background-color: rgba(252, 236, 236, 0.842);
    transition: ease 0.5s;
}

.fixed-donate p{
    font-size: 13px;
}

.fixed-donate-heart{
    color: #e05b0d;
}

.fixed-socialbar{
    position: fixed;
    bottom: 20%;
    width: 3vw;
    height: 20vh;
    left: 2vh;
    z-index: 99;
}

.fixed-socialbar img{
    padding-top: 1vh;
    height: 40px;
    width: 35px;
    cursor: pointer;
}

.fixed-edjustice-logo{
    position: fixed;
    top: 5vh;
    left: 3vw;
    z-index: 99;
}

.fixed-edjustice-logo img{
    height: 50px;
    width: 100px;
}

.nav-burger{
    z-index: 99;
    width: 100%;
}

.nav-burger img{
    z-index: 99;
    position: fixed;
    cursor: pointer;
    right: 0;
    margin-right: 2vw;
    margin-top: 3vh;
}

@media (max-width:768px){
    .nav-burger img{
        z-index: 99;
        position: fixed;
        cursor: pointer;
        right: 3vw;
        margin-right: 2vw;
        margin-top: 3vh;
    }    

    .fixed-donate{
        height: 6.5vh;
        right: 4vw;
    }
}