.ourReach{
    width: 90vw;
    height: 70vh;
}

.ourReach-heading{
    text-align: center;
    width: 100%;
    height: 10vh;
}

.ourReach-content{
    display: flex;
    vertical-align: middle;
}

.ourReach-map{
    height: 70vh;
    width: 50vw;
    padding-left: 10%;
}

.ourReach-map img{
    height: 100%;
}

.ourReach-placename{
    margin-left: 10vw;
    letter-spacing: 1px;
    line-height: 8vh;
}

.ourReach-path{
    height: 70vh;
    width: 30vw;
}

.ourReach-path img{
    height: 100%;
    width: 100%;
}

@media (max-width:768px){

    .ourReach{
        margin-top: 20vh;
        width: 100%;
        height: 100%;
    }
    
    .ourReach-heading{
        text-align: center;
        width: 100%;
        height: 10vh;
    }
    
    .ourReach-content{
        display: flex;
    }
    
    .ourReach-map{
        height: 70vh;
        width: 100%;
        padding-left: 0;
    }
    
    .ourReach-map img{
        height: 70%;
        width: 90%;
    }
    
    .ourReach-placename{
        display: none   ;
        margin-left: 10vw;
        letter-spacing: 1px;
        line-height: 8vh;
    }
    
    .ourReach-path{
        height: 70vh;
        width: 30vw;
        position: absolute;
        right: 5vw;
    }
    
    .ourReach-path img{
        height: 70%;
        width: 100%;
    }   
}