html{
    scroll-behavior: smooth;
}
.programLandingPage img{
    height: 110vh;
    width: 100%;
}

.programLandingPage-title{
    position: absolute;
    bottom: 5vh;
    text-align: center;
    width: 100%;
    color: white;
}
.programLandingPage-title h1{
    font-size: 5em;
}

.program-program1, .program-program2, .program-program3{
    height: 80vh;
    width: 100%;
    margin-top: 10vh;
    display: flex;
}

.program1-image img, .program2-image img, .program3-image img{
    height: 100%;
    width: 100%;
}

.program1-image, .program2-image, .program3-image{
    position: relative;
}

.program1-title, .program2-title, .program3-title{
    position: absolute;
    bottom: 10vh;
    font-size: 4em;
    color: white;
    text-align: center;
    width: 100%;
}

.program1-content,.program2-content, .program3-content{
    width: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    text-align: center;
    background-color: #6e96b8;
}

.program2-content{
    background-color: #e5e5e4 !important;
    color: #474747 !important;
}

.program3-content{
    background-color: #567b96 !important;
}

.program1-content p, .program2-content p, .program3-content p{
    font-size: 1.1em;
    padding-left: 5vw;
    font-weight: lighter;
    padding-right: 5vw;
}

.program1-content p, .program3-content p{
    color: white;
}

.program2-content p{
    color: #474747;
}

.program-ViewMoreButton{
    background-color: rgba(255, 97, 97, 1);
    color: white;
    padding: 2vh 2vh;
    border: none;
    margin-top: 3vh;
    width: 40%;
    font-style: italic;
    cursor: pointer;
    transition: 0.5s ease;
    outline: none;
}

.program-ViewMoreButton:hover{
    background-color: rgb(252, 113, 113);
}

.program1-MoreContent{
    height: 155vh;
    background-color: #6e96b8;
    display: none;
    padding-top: 10vh;
    padding-left: 15vw;
    color: white;
    padding-right: 15vw;
}

.program2-MoreContent{
    height: 115vh;
    background-color: #e5e5e4;
    display: none;
    padding-top: 10vh;
    padding-left: 15vw;
    padding-right: 15vw;
    text-align: center;
}

.program3-MoreContent{
    height: 100vh;
    background-color: #567b96;
    display: none;
    padding-top: 10vh;
    padding-left: 15vw;
    padding-right: 15vw;
    text-align: center;
    color: white;
}

.program1-MoreContent p, .program2-MoreContent p, .program3-MoreContent p{
    font-size: 1em;
    font-weight: lighter;
}

.program1-MoreContent ul, .program2-MoreContent ul, .program3-MoreContent ul{
    padding-left: 4vw;
}

.program-applybutton{
    
    background-color: rgba(255, 97, 97, 1);
    color: white;
    padding: 2vh 2vh;
    border: none;
    margin-top: 3vh;
    width: 30%;
    cursor: pointer;
    transition: 0.5s ease;
    outline: none;
    margin-top: 5vh;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.program-applybutton:hover{
    background-color: rgb(252, 113, 113);
}















