.workDone{
    background-image: url('/assets/image/home/workDonebg.jpg');
    margin-top: 20vh;
    height: 75vh;
    width: 100%;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.workDone-path{
    align-self: center;
    justify-content: center;
    display: flex;
}

.workDone-path img{
    margin-top: 10vh;
    width: 75%;
}

.workDone-content{
    width: 85%;
    display: flex;
    text-align: center;
    margin: 4vh 15vh;
    color: white;
}

.workDone-content-1,.workDone-content-2,.workDone-content-3,.workDone-content-4{
    width: 25%;
    padding-left: 2%;
    padding-right: 2%;
    
}

.workDone-content-heading{
    font-size: 2.2em;

}

.workDone-content-data{
    font-size: 1.2em;
    margin-top: 2vh;
}

@media (max-width:768px){
    .workDone{
        background-image: url('/assets/image/home/workDonebg.jpg');
        margin-top: -10vh;
        height: 100%;
        width: 100%;
        background-attachment: fixed;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
    
    .workDone-path{
        align-self: center;
        justify-content: center;
        display: flex;
    }
    
    .workDone-path img{
        margin-top: 10vh;
        width: 90%;
    }
    
    .workDone-content{
        width: 100%;
        display: flex;
        text-align: center;
        margin: 0;
        color: white;
    }
    
    .workDone-content-1,.workDone-content-2,.workDone-content-3,.workDone-content-4{
        width: 25%;
        text-align: center;
        padding-left: 0%;
        padding-right: 1%;
    }
    
    .workDone-content-heading{
        font-size: 1.5em;
    
    }
    
    .workDone-content-data{
        font-size: 2vh;
        margin: 0;
        font-weight: lighter;
    }
}