.shikshaYatra{
    width: 100%;
}

.yatraLanding{
    height: 100vh;
}

.yatraLanding img{
    width: 100%;
    height: 100%;
}

.yatra-heading{
    position: absolute;
    color: white;
    font-size: 5em;
    text-align: center;
    top: 40%;
    width: 100%;
}

.yatra-content{
    text-align: center;
    margin-top: 10vh;
    width: 100%;
}

.yatra-workshop-content{
    margin: 15vh 3vw;
    height: 100vh;
    position: relative;
    background-image: url(/assets/image/janShikshaYatra/yatraBackground.webp);
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.yatra-workshop-content-text{
    position: absolute;
    top: 0;
    background-color: rgba(0, 0, 0, 0.45);
    color: white;
    height: 70%;
    width: 80%;
    vertical-align: middle;
    top: 15%;
    text-align: center;
    padding: 5vh 8vw 5vh 8vw;
}

.yatra-workshop-content-text h1{
    font-weight: bolder;
}

.yatra-workshop-content-text p{
    margin: 5vh;
    font-size: 1.1em;
    font-weight: lighter;
}

.yatra-shiksha12{
    width: 100%;
}

.yatra-shiksha12-heading{
    text-align: center;
    width: 100%;
    margin-bottom: 8vh;
}

.ourReach-placename h2{
    font-size: 4vh;
}

.yatra-workdone .workDone-content-data{
    color: black;
}

.yatra-workdone-report{
    margin-top: 10vh;
    text-align: center;
    display: flex;
    margin-left: 20vw;
    margin-bottom: 10vh;
}

.yatra-workdone-report-content-number{
    font-weight: bolder;
    text-align: right;
}

.yatra-workdone-report-content-data{
    margin-left: 3vw;
}

.yatra-workdone-report-content-data{
    text-align: left;
    line-height: 6vh;
}

.yatra-workdone-report-content-data p{
    font-size: 1.2em;
    font-weight: lighter;
}

@media (max-width: 768px) {
    .yatraLanding{
        height: 50vh;
    }

    .yatraLanding img{
        height: 50vh;
    }
    .yatra-heading{
        font-size: 2em;
        color: white;
        top: 20% !important;
    }
    .yatra-content{
        padding: 0 4vw ;
    }
}